import { TopInfoTask } from "@/app/(home)/TopInfoTask"
import { Button } from "../ui/Button/Button"
import { TasksInProcess } from "../TasksInProcess/TasksInProcess"
import { fakeData } from "@/constants/fakeData"
import { useEffect, useState } from "react"
import { fetchConfig } from "@/api/fetchConfig"
import { api } from "@/pages/api/api"
import { useSelector } from "react-redux"
import { useAppSelector } from "@/hooks/useAppSelector"
import { APICOMMAND } from "@/shared/types/command.types"

export function Executor() {

  const user = useAppSelector((state) => state.user.user)
  const [tasks, setTasks] = useState()

  const handleClickGetAllTasks = async () => {
    const config = await fetchConfig()
    const response = await api(APICOMMAND.GETEXECUTORTASK, {'id': `${user?.id}`}, config)
    const data = await response?.json()
    setTasks(data)
  }

    return (
        <div className="flex flex-col items-start w-screen">
          <div className="w-full">
            <TopInfoTask/>
          </div>
          <div className="flex justify-between gap-[0px] w-260 mb-4">
            <Button onClick={handleClickGetAllTasks} classname="font-medium w-full h-15 flex justify-center items-center text-white cursor-pointer ease-in-out border-l-3 hover:border-l-3 hover:border-[#2B7FFF] hover:bg-[linear-gradient(to_right,_rgba(43,127,255,0.4),_rgba(43,127,255,0.2))]">
                <span className="text-gray-700">Задача в процессе</span>
            </Button>
            <Button classname="font-medium w-full h-15 flex justify-center items-center text-white cursor-pointer ease-in-out border-l-3 hover:border-l-3 hover:border-[#2B7FFF] hover:bg-[linear-gradient(to_right,_rgba(43,127,255,0.4),_rgba(43,127,255,0.2))]">
                <span className="text-gray-700">Завершенные задачи</span>
            </Button>
            <Button classname="font-medium w-full h-15 flex justify-center items-center text-white cursor-pointer ease-in-out border-l-3 hover:border-l-3 hover:border-[#2B7FFF] hover:bg-[linear-gradient(to_right,_rgba(43,127,255,0.4),_rgba(43,127,255,0.2))]">
                <span className="text-gray-700">Отменённые задачи</span>
            </Button>
            <Button classname="font-medium w-full h-15 flex justify-center items-center text-white cursor-pointer ease-in-out border-l-3 hover:border-l-3 hover:border-[#2B7FFF] hover:bg-[linear-gradient(to_right,_rgba(43,127,255,0.4),_rgba(43,127,255,0.2))]">
                <span className="text-gray-700">Все задачи</span>
            </Button>
          </div>
          <div className="w-full h-full overflow-x-auto overflow-y-auto">
            <TasksInProcess dataArr={fakeData}/>
          </div>
        </div>
    )
}
