'use client'

import { Task } from "@/shared/types/task.types"
import { ReactNode } from "react"

interface Props {
    data: Task
}

export function TabTasksInProcess({data}: Props) {
    return (
        <>
            <td className="border-r-1 border-gray-300 p-2">
                {data.priority}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.status}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.taskName}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.aboutInfo}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.dateOfCreate}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.dateOfClose}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.lastModifiedDate}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.docs}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.author}
            </td>
        </>
    )
}
